header {
  background-color: @primaryTextColor;
  color: #fff;
  height: 50px;
}

header a {
  color: #fff;
  font-weight: 600;
}

.header-home,
.header-navigation,
.header-user {
  display: inline-block;
}

.header-home,
.header-navigation {
  line-height: inherit;
}

.header-home a:before {
  font-family: FontAwesome;
  content: '\f015';
  float: left;
  font-weight: normal;
  font-size: 1.3em;
}

.header-user {
  float: right;
}

.header-topline {
  height: 200px;
  background-color: @darkPrimaryColor;
  color: #fff;
}

.header-navigation-item {
  display: inline-block;
}

.header-topline {
}

.header-topline h1 {
}

.header-topline h2 {
  color: @primaryTextColor;
}

.header-topline-logo:before {
  font-family: FontAwesome;
  font-size: 3em;
  text-shadow: 0px 0px 2px #000;
  margin-right: 15px;
  float: left;
}

.header-topline-headline {
  line-height: 28px;
}

.header-topline-leagues:before {
  content: '\f091';
}